body {
  padding: 60px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
* {
  font-family: Helvetica, Arial, sans-serif;
}
input {
  font-size: 1.2rem;
  width: 100%;
  color: gray;
}
.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.card {
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
  width: 300px;
}
.card p {
  padding: 5px;
  text-align: center;
}
.card img {
  max-height: 170px;
  width: 300px
}

